
const toPairs = require("ramda/src/toPairs")
const mapObjIndexed = require("ramda/src/mapObjIndexed")
const values = require("ramda/src/values")
const mergeDeepRight = require("ramda/src/mergeDeepRight")
import random from "./randomengine"
import {ChargeSheetChain, Classifications, Outcomes, IncarcerationRate} from "./chargesheetdata"


const chain = ChargeSheetChain;

const genSheet = (lead = getLead())=>{
   
    const charges = [lead];
    
    let nextCharge = lead

    while(nextCharge != "end"){
     //   let newCharge = getNextCharge(lead)
        let newCharge = getNextCharge(nextCharge)
        if (newCharge != "end"){charges.push(newCharge)}
        nextCharge = newCharge
       // console.log(nextCharge)
    }

    //classifier
    const output = charges.map(function(charge){
     
        return {
            name: charge,
            severity: random.weighted(Classifications[charge]["severity"]),
            violent: Classifications[charge]["violent"]
        }
    })

    return output;
}

const genPastSheet = (yearsAgo)=>{
    const sheet = genSheet();
    let pending = (yearsAgo <= 1) ? random.bool(.7) : false
    let isGuilty = false
    const withDispositions = sheet.map(function(charge){
        const result = pending ? "Pending" : random.weighted(Outcomes);
        if(result == "Guilty"){isGuilty = true};
        const output = mergeDeepRight(charge)({disposition: result})
        return output
    })
    const incarcerated = isGuilty == true ? random.bool(IncarcerationRate) : false

    let ftas = 0
    const ftaChances = 0.2
    let hadFta = random.bool(ftaChances)
    while(hadFta==true){
        ftas+=1
        hadFta = random.bool(ftaChances)
    }

    const output = {
        charges: withDispositions,
        incarcerated: incarcerated,
        ftas: 0,
    }
    return output

}


const getNextCharge = (charge) => {
    const weightedPairs = toPairs(chain[charge]["next"])
    const next = random.weighted(weightedPairs)
    return next
}

const getLead = ()=>{
    const derivePair = (value, key, obj) =>{
        return [key, value["count"]]
    }
    const pairs = mapObjIndexed(derivePair,chain)
    const output = random.weighted(values(pairs))
    return output
}

export  {genSheet, genPastSheet}