import random from "../randomengine"
import split from 'ramda/src/split'
import last from 'ramda/src/last'
import tail from 'ramda/src/tail'
import head from 'ramda/src/head'
import toLower from 'ramda/src/toLower'
import concat from 'ramda/src/concat'


const generateViolenceHeadline = (defendant, settings) => {
    const pronoun = defendant.sex === "M" ? "man" : "woman";
    const crime = defendant.charge === "unlawful use of a weapon" ? "weapons" : defendant.charge;

    const newCrime = random.pick([
       "murder",
       "assault",
       "burglary",
       "manslaughter",
       "aggravated assault",
       "battery"
    ])

    const jailedLede = [
        `${pronoun} incarcerated after violent assault`,
        `${pronoun} jailed after horrific act of violence`,
        `Violence rocks Cherwell County after ${crime} suspect released `,
        `Area ${pronoun}, charged with ${crime} case, arrested on suspicions of ${newCrime}.`,
        `Cherwell ${pronoun} arrested for violent crime while awaiting trial on ${crime} charges.`,
    ]
        
    const atLargeLede = [
        `Defendant in ${crime} case suspected in new violent incident.`,
        `Violent suspect at large thanks to activist judge`,
        `Court releases criminal, now suspected of ${newCrime}`,
        `County sheriff seeks public's help tracking down violent defendant`,
    ]

    const headline = settings.detained === true ? random.pick(jailedLede) : random.pick(atLargeLede)
    const copy = generateCopy(defendant, settings, newCrime)
    const output = {
        headline: headline,
        copy: copy
    }
    return output
}


const generateCopy = (defendant, settings, newCrime) => {
    const lastName = (name)=>{
        const names = split(" ", name);
        const lastNames = last(names);
        const tailOf = tail(lastNames);
        const output = concat(head(lastNames), toLower(tailOf))
        return output
    }
    const jailed = `${defendant.name}, ${defendant.age}, was arrested on suspicion of ${newCrime} yesterday. ${lastName(defendant.name)} was awaiting trial for ${defendant.charge}, and was released into the community by a local judge.`

    const atlarge = `Cherwell County law enforcement have issued a warrant for ${defendant.name}, ${defendant.age}, on suspicion of ${newCrime}. It is unclear why a local judge released ${lastName(defendant.name)} into the community to commit this new act of violence, instead of detaining ${defendant.sex == "M" ? "him" : "her"} while ${defendant.sex == "M" ? "he" : "she"} awaited trial for ${defendant.charge}.`

    const output = settings.detained == true ? jailed : atlarge

    return output

}


export default generateViolenceHeadline



