import random from "../randomengine"
import split from 'ramda/src/split'
import last from 'ramda/src/last'
import tail from 'ramda/src/tail'
import head from 'ramda/src/head'
import toLower from 'ramda/src/toLower'
import concat from 'ramda/src/concat'


const generateCrimeHeadline = (defendant, settings) => {
    const pronoun = defendant.sex === "M" ? "man" : "woman";
    const crime = defendant.charge === "unlawful use of a weapon" ? "weapons" : defendant.charge;

    const newCrime = random.pick([
        "fraud",
        "forgery",
        "drug trafficking",
        "burglary",
        "drug possession",
        "unlawful use of a weapon",
        "reckless driving"
    ])

    const jailedLede = [
        `${pronoun} incarcerated after committing new crime.`,
        `${crime} suspect arrested after on suspicion of new offense.`,
        `Area ${pronoun}, charged with ${crime} case, arrested for new crime.`,
        `Cherwell ${pronoun} arrested for new crime while awaiting trial on ${crime} charges.`,
    ]
        
    const atLargeLede = [
        `Defendant in ${crime} case suspected in new ${newCrime} incident.`,
        `Warrant issued after area defendant suspected of new crime`,
        `Court releases criminal, who promptly commits new crime`,
        `County sherriff seeks public's help tracking down serial criminal`,
        `Suspect in ${crime} case accused of new crime. Warrant to be issued.`
    ]

    const headline = settings.detained === true ? random.pick(jailedLede) : random.pick(atLargeLede)
    const copy = generateCopy(defendant, settings, newCrime)
    const output = {
        headline: headline,
        copy: copy
    }
    return output
}

const generateCopy = (defendant, settings, newCrime) => {
    const lastName = (name)=>{
        const names = split(" ", name);
        const lastNames = last(names);
        const tailOf = tail(lastNames);
        const output = concat(head(lastNames), toLower(tailOf))
        return output
    }
    const jailed = `${defendant.name}, ${defendant.age}, was arrested on suspicion of ${newCrime} yesterday. ${lastName(defendant.name)} was already facing trial for ${defendant.charge}, and had been released on their own recognizance. `

    const atlarge = `Cherwell County law enforcement have issued a warrant for ${defendant.name}, ${defendant.age}, on suspicion of ${newCrime}. ${lastName(defendant.name)} is already awaiting trial in a ${defendant.charge} case.`

    const output = settings.detained == true ? jailed : atlarge

    return output

}

export default generateCrimeHeadline



