import generateCrimeHeadline from "./headlines/crime"
import generateFtaHeadline from "./headlines/fta"
import generateViolenceHeadline from "./headlines/violence"

import random from "./randomengine"

const Headline = {
    schema: {
        type: "", //FTA, Crime, Violence, Agitation
        headline: "", 
        subject: "", //card target
        photo: "",  //
        content: "",
        edits: {
            jail: 0, // how much will jail pop change on open
            fear: 0 // how much will fear change on open
        }
    },
    generate: (type, defendant = {}) => {
        console.log("milestone H1")
        const settings = Headline.settings.severity
        const detained = random.bool((settings.detained_discount[type]/100))
        const severity = Headline.get_severity(type, defendant, detained)
        const text = Headline.get_text(type, defendant, detained);
        console.log("milestone H4")

        console.log({type:"notice", content: type, headline:text.headline, copy:text.copy, defendant: defendant, severity: severity, detained:detained})
        return {type:"notice", content: type, headline:text.headline, copy:text.copy, defendant: defendant, severity: severity, detained:detained}
    },
    get_severity: (type, defendant, detained) => {
        console.log("milestone H2")

        const settings = Headline.settings.severity
        const base_severity = random.integer(settings.base[type], (settings.base[type] + settings.variance[type]))
        const bonus = settings.crime_bonus[defendant.charge]
        const output = ( (detained ? settings.detained_discount[type] : 1) * (base_severity+bonus) ) / 100
        console.log(output)
        return output; 
    },
    get_text: (type, defendant, detained) => {
        console.log("milestone H3")
        switch (type){
            case "fta":
                return generateFtaHeadline(defendant, {detained: detained})
            case "crime":
                return generateCrimeHeadline(defendant, {detained: detained})
            case "violence":
                return generateViolenceHeadline(defendant, {detained: detained})

        }
    },
    settings: {
        severity: {
            base: {
                fta: 5,
                crime: 10,
                violence: 25
            },
            variance:{
                fta: 0,
                crime: 5,
                violence: 10
            },
            detained_discount:{
                fta: 0.5,
                crime: 0.7, //70% of base
                violence: 0.9
            },
            crime_bonus: {
                "murder": 10,
                "rape": 8,
                "robbery": 5,
                "assault": 3,
                "burglary": 3,
                "theft": 3,
                "motor vehicle theft": 2,
                "forgery": 0,
                "fraud": 0,
                "drug trafficking": 2,
                "drug possession": 1,
                "unlawful use of a weapon": 2,
                "reckless driving": 0,
            }
        }
    }
}

export default Headline