import random from "./randomengine"
import values from 'ramda/src/values'
import map from 'ramda/src/map'
import sum from 'ramda/src/sum'



const generateProsecution = (risk) => {
    const quantifyRisk = {
           "low": 1,
           "medium": 3,
           "high": 5
    }
    const riskTotal = (risk) => {
        const riskValues = values(risk)
        const quantify = (r) => {return quantifyRisk[r]}
        const quantities = map(quantify, riskValues)
        return sum(quantities)
    }

  //  console.log(riskTotal(risk))

    if(risk.violence == "high"){
        return {rec: "detain", commentary: "Defendant is an extreme danger to others."}
    } else if(risk.crime == "high"){
        return {rec: "detain", commentary: "Defendant is very likely to commit a new crime if released."}
    } else if(risk.fta == "high"){
        return {rec: "detain", commentary: "Defendant is an extreme flight risk."}
    } else if(riskTotal(risk) == 3){
            const detain = random.bool(.05)
            return detain ? {rec: "detain", commentary: "The court's tool underestimates the defendant's risk."} : {rec: "release", commentary: ""}
    } else if (riskTotal(risk) == 5){
            const detain = random.bool(.4)
            return detain ? {rec: "detain", commentary: ""} : {rec: "release", commentary: ""}
    } else if (riskTotal(risk) == 7){
            const detain = random.bool(.95)
            return detain ? {rec: "detain", commentary: ""} : {rec: "release", commentary: ""}
    } else if (riskTotal(risk) == 9){
            return {rec: "detain", commentary: ""}
    } else {
            return {rec: "detain", commentary: ""}
    }
     
}

export default generateProsecution