//code for building your own
import m from "mithril"
import range from "ramda/src/range"
import mergeDeepRight from "ramda/src/mergeDeepRight"
import Generate from "./models/generators/generators"
import {genSheet, genPastSheet} from "./models/generators/gensheet"
import random from "./models/generators/randomengine"
const stream = require("mithril/stream")


const Model = {
    defendant: stream({}),
    assessmentType: stream("psa"),
    editable: false,
    risk:{}
}

Model["generateDefendant"] = ()=>{
    //first, get biographical info
    const sex = Generate["sex"]()
    const name = Generate["name"]({sex: sex})
    const photo = Generate["photo"]({sex: sex})
    const age = Generate["age"]()

    //second, generate current charges
    const current = genSheet()

    //third, get criminal history
    let pastCharges = []
    let history = age-18
    let past = random.bool(.5)
    while(past == true && history > 0){
        const yearsAgo = random.integer(1, history, false)
        const pastSheet = genPastSheet()
        const arrestYear = (new Date()).getFullYear() - yearsAgo
        const output = mergeDeepRight({date: arrestYear})(pastSheet)
        pastCharges.push(output)
        past = random.bool(.3);
        history = history - yearsAgo;
    }

    const output = {
        name: name,
        sex: sex,
        photo: photo,
        age: age,
        currentCharges: current,
        pastCharges: pastCharges,
    }
    
    Model.defendant(output);
    console.log(Model.defendant())


    //finally, get risk assessment
}

Model["init"]=()=>{
    Model["generateDefendant"]();
    Model["risk"] = stream.combine(
        function(defendant, type){
            if (defendant()!={}){
                return Generate["risk"][type()](defendant())
            }
            else{
                return {}
            }
        }, 
        [Model.defendant, Model.assessmentType]
    )
}
    
    
    






const DefendantView = {
    oninit: Model.init,
    view: (vnode)=>{
        const defendant = Model.defendant()
        const risk = Model.risk() 
        //const risk = vnode.attrs.risk
       return m("article", {class: "cf helvetica mw8 center justify-between flex flex-wrap"}, [
            m(TopButtons),
            m(Bio, {defendant:defendant}),
            m(CurrentCharges, {defendant:defendant}), 
            m(PastCharges, {defendant:defendant}),
            m(RiskAssessment[Model.assessmentType], {defendant:defendant, risk: risk})
        ])
    }
}

