import random from "../randomengine"

const generateFtaHeadline = (defendant, settings) => {
    const pronoun = defendant.sex === "M" ? "man" : "woman";
    const crime = defendant.charge === "unlawful use of a weapon" ? "weapons" : defendant.charge;

    const jailedLede = [
        `${pronoun} incarcerated after failure to appear for hearing.`,
        `${crime} suspect arrested after failing to appear in court.`,
        `Area ${pronoun} arrested for failing to appear in court.`,
        `Cherwell ${pronoun} arrested on warrant for failure to appear.`,
        `Cherwell City ${pronoun} wanted for failing to appear in court arrested.`,
    ]
        
    const atLargeLede = [
        `${pronoun} fails to show for court hearing.`,
        `Warrant issued after suspect fails to appear in Court for plea hearing`,
        `Cherwell County ${crime} suspect fails to appear for court hearing`,
        `${pronoun} accused of ${crime} skips pre-trial hearing`,
       // `Cherwell County suspect fails to show in court, found dead`,
        `Cherwell ${pronoun} wanted for failure to appear in court on ${crime} charges`,
        `Defendant a no-show in ${crime} case `,
        `Cherwell Co. sheriff seeks ${pronoun} who failed to appear in court `,
        `Suspect in ${crime} case fails to appear in court. Warrant to be issued.`
    ]

    const headline = settings.detained === true ? random.pick(jailedLede) : random.pick(atLargeLede)
    const copy = generateCopy(defendant, settings)
    const output = {
        headline: headline,
        copy: copy
    }
    return output
}

const generateCopy = (defendant, settings) => {
            const output = `${defendant.name}, ${defendant.age}, failed to show up for a pre-trial hearing yesterday, where ${defendant.sex=="M" ? "he" : "she"} faces ${defendant.charge} charges. ${settings.detained == true ? `A bench warrant was issued for ${defendant.sex=="M" ? "his" : "her"} arrest, and ${defendant.sex=="M" ? "he" : "she"} was detained in Cherwell County Jail early yesterday evening.` : `A bench warrant was issued, but the suspect's whereabouts are currently unknown.`}`

            return output

}

export default generateFtaHeadline



