import random from "./randomengine"


const generateDefense = (defendant) => {
    
   const reasons = ["illness", "job", "housing", "family", "fight"]
    
   const content = {
       illness: () => {
           const first = [
               `I'm very ill.`,
               `I'm sick.`,
               `I need medical attention.`,
           ]
           const second = [
               `If I'm detained,`,
               `If I'm not released,`,
               `If I'm jailed, `
           ]
           const third = [
               `I could get a lot sicker`,
               `I may not recover`,
               `my illness might get worse`,
               `I can't get the help I need`,
               `I won't be able to get the help I need`
           ]
           return `${random.pick(first)} ${random.pick(second)} ${random.pick(third)}.`
       },
       job: () => {
            const first = [
                `If I'm detained and I`,
                `If I'm not released and I`,
                `If I go to jail and I`,
                `I can't afford to be detained. If I`
            ]
            const second = [
                `miss one more day of work`,
                `miss my shift`,
                `miss another day of work`,
                `don't show up to work`,
                `don't come to work`,
                `can't work`
            ]
            const third = [
                `my boss will fire me`,
                `I'll get fired`,
                `I'll lose my job`,
                `I won't be able to find another job`
            ]
            return `${random.pick(first)} ${random.pick(second)}, ${random.pick(third)}.`
       },
       housing: () => {
        const first = [
            `If I'm detained`,
            `If you send me to jail`,
            `If I go to jail`
        ]
        const second = [
            `I can't work`,
            `there's no one else to work`,
            `I can't support my family`,
        ]
        const third = [
            `and I won't be able to pay the mortgage`,
            `and I'll lose my home`,
            `and my family will be out on the street`,
            `and I won't have a home to come back to`,
            `and my landlord will evict me`,
            `and I'll start missing rent payments`
        ]
        return `${random.pick(first)}, ${random.pick(second)}, ${random.pick(third)}.`
       },
       family: () => {
        const first = [
            `I'll lose custody of my children`,
            `My family will leave me`
        ]
        const second = [
            `if I get sent to jail`,
            `if I'm locked up again`,
            `if you detain me`
        ]
       
        const output =  `${random.pick(first)} ${random.pick(second)}.`
        return output
       },
       fight: () => {
        const first = [
            `The case against me is weak`,
            `The government has no evidence`,
            `The state's case is weak, and they know it`
        ]
        const second = [
            `I want to fight these charges. If I'm detained, I can't afford to have my day in front of a jury. `,
            `The DA is trying to get me to confess to a crime I didn't commit. Don't make his job easier.`
        ]
        return `${random.pick(first)}. ${random.pick(second)}`
       }
   }
   
    const reason = random.pick(reasons)
    //console.log(reason)
    const output = content[reason]()
    return {statement: output, reason: reason}
    
}

export default generateDefense



